<script>
  import { onDestroy, createEventDispatcher } from "svelte";

  import { meetupStore } from "../../stores/meetups.js";
  import Button from "../Button";

  // Id of the meetup.
  export let id;

  let selectedMeetup;

  const unsubscribe = meetupStore.subscribe(items => {
    selectedMeetup = items.find(i => i.id === id);
  });

  // Event dispatcher and custom events dispatched by this component.
  const dispatch = createEventDispatcher();
  const CLOSE_EVENT = 'close';

  const closeMeetup = () => {
    dispatch(CLOSE_EVENT)
  }

  onDestroy(() => {
    unsubscribe();
  });
</script>

<style src="./style.css">section {
  margin-top: 4rem;
}

.image {
  height: 25rem;
  width: 100%;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.image {
  background: #e7e7e7;
}

.content {
  margin: auto;
  text-align: center;
  width: 80%;
}

h1 {
  font-family: "Roboto Slab", sans-serif;
  font-size: 2rem;
  margin: 0.5rem 0;
}

h2 {
  color: #6b6b6b;
  font-size: 1.25rem;
}

p {
  font-size: 1.5rem;
}
</style>

<section>
  <div class="image">
    <img alt={selectedMeetup.title} src={selectedMeetup.imageUrl} />
  </div>
  <div class="content">
    <h1>{selectedMeetup.title}</h1>
    <h2>{selectedMeetup.subtitle} - {selectedMeetup.address}</h2>
    <p>{selectedMeetup.description}</p>
    <Button href="mailto:{selectedMeetup.email}">Contact</Button>
    <Button mode="outline" on:click={closeMeetup}>
      Close
    </Button>
  </div>
</section>
